<template lang="html">
  <div class="">
    <section class="content">
      <div class="clearfix">
        <tabCommon :tabs="otherTab" class="fl con-tab" @conTabChange="tabHandle"></tabCommon>
      </div>
      <resList :infos="infos"></resList>
    </section>
    <div class="block clearfix pager">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        class="fr">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import tabCommon from '../component/tab-common.vue'
import resList from '../component/res-list.vue'

import {resApi, newsApi, blogApi} from '../api/api.js'

export default {
  data () {
    return {
      msg: 'DEMO',
      currentPage: 1,
      total: 0,
      size: 10,
      page: 1,
      index: 0,
      otherTab: [
        {'title': '', 'current': true},
        {'title': '', 'current': false}
      ],
      infos: ''
    }
  },
  components: {
    tabCommon,
    resList
  },
  methods: {
    init: function () {
      let param = `size=10&page=1`
      resApi(param).then((res) => {
        this.infos = res.data
        this.total = res.total
        console.log(res)
      })
    },

    handleSizeChange(val) {
      console.log(` ${val} `)
      this.size = val
      this.loadData()
    },

    handleCurrentChange(val) {
      console.log(`: ${val}`)
      this.page = val
      this.loadData()
    },

    tabHandle: function (index) {
      this.otherTab = this.otherTab.map((item) => {
        item.current = false
        return item
      })
      this.otherTab[index].current = true
      this.index = index

      this.loadData()
    },

    loadData: function () {
      let param = `size=${this.size}&page=${this.page}`
      if (this.index === 0) {
        resApi(param).then((res) => {
          this.infos = res.data
          this.total = res.total
        })
      } else if (this.index === 1) {
        newsApi(param).then((res) => {
          this.infos = res.data
          this.total = res.total
        })
      } else {
        blogApi(param).then((res) => {
          this.infos = res.data
          this.total = res.total
        })
      }
    }
  },

  mounted: function () {
    this.init()
  }
}
</script>



